.sidebar {
	display: flex;
	flex-direction: column;
	height: 100vh;
	padding: 20px;
	background-color: #2c3e50;
	color: white;
	overflow-y: auto;
}

.sidebar-header {
	text-align: center;
}

.sidebar-menu {
	list-style-type: none;
	padding: 0;
	flex-grow: 1;
}

.sidebar-menu li {
	margin: 15px 0;
}

.sidebar-menu li a {
	color: #bdc3c7;
	text-decoration: none;
	display: block;
	padding: 10px;
	border-radius: 4px;
}

.sidebar-menu li a.active {
	background-color: #1abc9c;
	color: white;
}

.user-info {
	display: flex;
	flex-direction: column;
	/* Ensure vertical alignment */
	align-items: center;
	/* Center horizontally */
	margin-bottom: 20px;
}

.user-info img {
	width: 60px;
	/* Increase the size */
	height: 60px;
	/* Maintain the aspect ratio */
	border-radius: 50%;
	margin-bottom: 10px;
	/* Space between avatar and username */
}

.separator {
	height: 1px;
	width: 100%;
	background-color: #bdc3c7;
	margin: 20px 0;
	/* Space around the separator */
}

.logout-container {
	margin-bottom: 30px;
}

.logout-button {
	background: none;
	border: none;
	color: #bdc3c7;
	cursor: pointer;
	padding: 10px;
	text-align: left;
	width: 100%;
	display: block;
	/* Ensure it takes full width */
	text-decoration: none;
	/* Remove underline */
}

.logout-button:hover {
	text-decoration: underline;
	color: #007bff;
}