.container {
	padding: 20px;
}

.asset-list {
	list-style: none;
	padding: 0;
}

.asset-list li {
	display: flex;
	justify-content: space-between;
	padding: 10px 0;
	border-bottom: 1px solid #e8e8e8;
}

.button-container {
	margin-top: 20px;
}

.map-container {
	margin-top: 20px;
	height: 60vh;
}

.modal-content {
	padding: 20px;
}

.asset-info p {
	margin: 0;
	padding: 5px 0;
}

.form-group {
	margin-bottom: 15px;
}