.welcome-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 20px;
	width: 90%;
	margin: 0 auto;
}

.form-container {
	width: 90%;
	margin-top: 20px;
}

.search-input {
	width: 90%;
	padding: 10px;
	border-radius: 4px;
	border: 1px solid #ccc;
}

.map-container {
	width: 90%;
	height: 400px;
	margin-top: 20px;
	border: 1px solid #ccc;
}

.button-container {
	margin-top: 20px;
	text-align: center;
}